            </div>
            <div class='header__column'>
                <span class='header__time'>20:33</span>
            </div>
            <div class='header__column'>
                <i class='far fa-moon'></i>
                <i class='fa fa-bluetooth-b'></i>
                <span class='header__battery'>100% <i class='fas fa-battery-full'></i></span>
            </div>
        </div>
        <div class='header__bottom'>
            <div class='header__column'>
                <span class='header__text'>{{menu}}</span>
            </div>
            <div class='header__column'>
                <span class='header__text'>
                    {{title}}
                    <span class='header__number'>{{num}}</span>
                </span>
            </div>
            <div class='header__column'>
                <i :class='icon'></i>
            </div>
        </div>
    </header>
    <router-view></router-view>
   <nav class='tab-bar'>
        <router-link to='/friends' class='tab-bar__tab'>
            <i class='fas fa-user'></i>
