import Vue from 'vue'
import VueRouter from 'vue-router'
import App from '../App.vue'

import Chats from '../components/Chats.vue'
import Find from '../components/Find.vue'
import Friends from '../components/Friends.vue'
import More from '../components/More.vue'

import Profile from '../components/Profile.vue'
import Chatting from '../components/Chatting.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', component: App, redirect: '/friends' },
  { path: '/friends', component: Friends, name: 'friends' },
  { path: '/chats', component: Chats, name: 'chats' },
  { path: '/find', component: Find, name: 'find' },
  { path: '/more', component: More, name: 'more' },
  { path: '/profile', component: Profile },
  { path: '/chatting', component: Chatting }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: 'tab-bar__tab--selected',
  linkExactActiveClass: 'tab-bar__tab'
})

export default router
